.banner {
    background: url(./banner.png);
    height: 800px;
    background-size: cover;
    margin-top: -90px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-direction: column;
        flex-direction: column;
    color: #fff;
    text-align: center;
}

@media (max-width: 767px) {
    .banner {
        margin-top: -125px;
        background-position: 20%;
    }
}

.banner h1 {
    font-size: 5rem;
    margin: 0;
}

.banner h3 {
    font-size: 2rem;
    margin: 0 0 20px;
}

.banner .icon {
    width: 2rem;
    vertical-align: middle;
}

.banner .date,
.banner .location {
    padding: 10px 20px;
}

.banner .register {
    background: transparent;
    border: 1px solid #fff;
    border-radius: 20px;
    color: white;
    padding: 5px 15px;
    font-size: 0.9rem;
    text-transform: uppercase;
    margin-top: 2rem;
}

.banner .register .icon {
    width: 1rem;
}


