header {
    padding: 0 4rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: justify;
        justify-content: space-between;
    border-bottom: 1px solid #fff;
    background: transparent;
    position: relative;
}

.logo {
    width: 9rem;
}

.navigation a {
    padding: 0 12px;
    text-decoration: none;
    color: white;
}

@media (max-width: 767px) {
    header {
        display: block;
        text-align: center;
        padding: 0;
    }

    .logo {
        margin: 0 auto;
        display: block;
    }

    .navigation {
        margin-bottom: 20px;
    }
}



